@media all and (max-width: 960px) {
  .container {
    width: 100%;
  }

  .profile {
    margin-top: 100px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .cv-block {
    margin-left: 0px;
    padding: 15px;
  }
  .about-section {
    height: auto;
  }
  .item {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .navbar {
    width: 100%;
    padding-left: 10%;
    padding-right: 10%;
    justify-content: center;
  }

  .header-items {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 75px;
  }

  .copyrigt-items {
    flex-direction: column;
    justify-content: center;
  }
  .social-search-items {
    justify-content: space-around;
  }
  .text-block {
    justify-content: center;
    height: unset;
    width: unset;
  }
}

@media all and (max-width: 398px) {
}
@media all and (max-width: 320px) {
  .service {
    width: 100%;
  }
  .search-items {
    flex-direction: column;
  }
  .subscribe-element {
    width: 100%;
  }
  .subs-name-input {
    width: 100%;
  }
  .subs-mail-input {
    width: 100%;
  }
  .work-items {
    width: 100%;
  }
  .item {
    width: 100%;
    height: unset;
  }
  .footer-element {
    width: 100%;
  }

  .search-input {
    width: 100%;
  }
  .logo {
    width: 100%;
  }
}
