* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  width: 960px;
  margin: auto;
}

.top-border {
  width: 100%;
  height: 5px;
  background-color: #57068c;
  position: fixed;
}

header {
  width: 100%;
  background-color: #ffffff;
  border-bottom: solid 1px;
  border-color: #e2e2e2;
  position: fixed;
  z-index: 100;
}

.header-items {
  height: 75px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.logo {
  width: 155px;
  color: white;
  font: 28px Georgia;
  background: none;
  z-index: 100;
}

.navbar {
  width: 400px;
  z-index: 100;
}

.about-section {
  height: 100vh;
  background: tomato;
  width: 100%;
}

.contact-section {
  height: 100vh;
  background: gainsboro;
  width: 100%;
}

.research-section {
  height: 100vh;
  background: yellowgreen;
  width: 100%;
}

.footer-section {
  height: 76px;
  background: black;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.menu {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.button {
  width: fit-content;
  height: fit-content;
  border-radius: 5px;
  font: 12px GEORGIA;
  text-decoration: none;
  color: #999999;
}

.button:hover {
  color: #57068c;
  cursor: pointer;
}

a.active {
  color: white;
  border-radius: 8px;
  padding: 7px;
  text-align: center;
  background-color: #57068c;
  text-transform: uppercase;
  font: 14px GEORGIA;
}

.copyrigt-section {
  background-color: #2b2b2b;
  display: flex;
  height: 100%;
}

.copyrigt-text {
  display: flex;
  font: 12px GEORGIA;
  color: #979797;
  align-items: center;
  justify-content: center;
}

.arsmen {
  display: flex;
  height: 100%;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.ar {
  font: 800px GEORGIA;
  text-align: center;
  color: blue;
  padding: 10px;
}

.image-cropper {
  width: 300px;
  height: 300px;
  overflow: hidden;
  position: relative;
  border-radius: 50%;
}

.profile-section {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.profile {
  margin-top: 75px;
  display: flex;
}

.name {
  font: 29px GEORGIA;
  text-align: center;
}

.title {
  font: 18px GEORGIA;
  text-align: center;
}

.image {
  display: inline;
  margin: 0 auto;
  height: 100%;
  width: auto;
}

.cv-block {
  margin-left: 40px;
}
h2 {
  font-size: 29px;
}

.item {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.a {
  display: flex;
}

.b {
  margin-left: 10px;
  width: 200px;
}

.c {
  width: 100px;
}

.d {
  text-align: right;
}

.res {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}
